.header {
  height: 100vh;
  background-image: url(../images/cover_photo_pnone.png);
  background-size: cover;
  background-position: 90%;

  @include on-tablet {
    background-image: url(../images/Cover_ptoto_tablet_2.png);
    height: 628px;
    background-position: 50%;
  }

  @include on-laptop {
    background-image: url(../images/Cover_photo.png);
    background-position: center;
    height: 120vh;
  }

  &__title {
    @include on-phone {
      margin-top: 100%;
    }

    display: flex;
    justify-content: center;
    color: $main-text-color;
    font-size: 36px;
    font-weight: 700;
    white-space: nowrap;

    &__content {
      @include content-padding-inline;
    }

    @include on-tablet {
      font-size: 64px;
      font-weight: 700;
      margin-top: 40%;
    }

    @include on-laptop {
      font-size: 80px;
      font-weight: 700;
      margin-top: 30%;
    }
  }
}

img:hover {
  transform: scale(1.1);
  transition: transform $effectDuration;
}
