input,
textarea,
select {
  border: 1px solid #ccc;
  background-color: #F8F8FA;

  color: #292929;
  font-weight: 400;
  transition:
    background-color 0.3s,
    color 0.3s;
}

input:-webkit-autofill,
textarea:-webkit-autofill,
select:-webkit-autofill {
  border: 1px solid white;
  -webkit-text-fill-color: #292929;
  -webkit-box-shadow: 0 0 0 1000px #F8F8FA;
  font-weight: 400;
  transition:
    background-color 0.3s,
    color 0.3s;
}

input:valid,
textarea:valid,
select:valid {
  border: 1px solid #fff;
  background-color: #F8F8FA;
  color: #292929;
  font-weight: 400;
}

input:focus:not(:placeholder-shown),
textarea:focus:not(:placeholder-shown),
select:focus:not(:placeholder-shown) {
  border: 1px solid #fff;
  background-color: #F8F8FA;
  color: #292929;
  font-weight: 400;
}


.form {
  display: flex;
  flex-direction: column;
  gap: 10px;

  &__input--message {
    height: 150px;
    padding-top: 15px;
    resize: none;
    outline: none;
  }

  &__input {
    font-family: Poppins, sans-serif;
    cursor: pointer;
    border-radius: 30px;
    padding-left: 10px;
    color: #7e7e83;
    font-size: 16px;
    font-weight: 400;
    width: 278px;
    box-sizing: border-box;

    @include on-tablet {
      width: 288px;
    }

    @include on-laptop {
      width: 411px;
    }

    &--item {
      height: 48px;
    }

    &:hover {
      color: #292929;

      &::placeholder {
        color: #292929;
      }
    }
  }

  &__button {
    font-family: Poppins, sans-serif;
    cursor: pointer;
    height: 58px;
    margin-top: 30px;
    border-radius: 30px;
    font-size: 16px;
    font-weight: 700;
    width: 278px;
    box-sizing: border-box;

    @include on-tablet {
      width: 288px;
    }

    @include on-laptop {
      width: 411px;
    }

    &:hover {
      box-shadow: 1px 4px 16px 1px #e8e8f066;
    }
  }
}

.example {
  color: $main-text-color;
  &__link {
    color: $main-text-color;
    text-decoration: none;

    &:hover {
      color: #7e7e83;
    }
  }

  &__item {
    &--smaller {
      font-size: 12px;
      font-weight: 400;
    }

    &--bigger {
      font-size: 16px;
      font-weight: 500;
    }
  }

  @include on-phone {
    @media (max-width: 576px) {
      margin-top: 48px;
    }
  }
}

.contacts {
  @include on-tablet {
    &__container {
      display: flex;
      gap: 34px;
    }
  }

  @include on-laptop {
    &__container {
      gap: 198px;
    }
  }
}
