* {
  transition: all 0.3s ease;
}

body {
  font-family: Poppins, sans-serif;
}

.main {
  &__content {
    display: grid;
    row-gap: 92px;
    padding-block: 92px;

    @include on-tablet {
      padding-block: 120px;
      row-gap: 120px;
    }
  }
}

button,
input {
  border: none;
  outline: none;
}
